

import React, { Component } from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import * as commonActions from '../../state/commonActions';
import Icons from '../Icons/Icons';
import './style.css';


var data = [
  {
    title:"",
    description:["Commit to TWO 12-week sessions with a one month break in between","Session 1: Front-end Web Development [REQUIRED]","Session 2: One of several options in programming languages such as C#, JavaScript, Python, or more"],
    class:"mobile-slide-stuff-done"
  },
  {
    title:"",
    description:["Complete 20-30 hours of pre-work issued 45-60 days before your course starts and due 4 weeks after assignment. The pre-work consists of Treehouse courses covering HTML, CSS, and JavaScript. This material will build a foundation of knowledge and also give you enough exposure to see if writing software is for you"],
    class:"mobile-slide-share"
  },
  {
    title:"",
    description:["Attend meetups once per week (6:00 - 8:00 pm) at our hosted space in Downtown Louisville (other locations may also be available as space allows)"],
    class:"mobile-slide-edit"
  },
  {
    title:"",
    description:["During the course you will complete an online curriculum through Treehouse, attend tech events around town, attend class meetups, and build a project"],
    class:"mobile-slide-chat"
  },
  {
    title:"",
    description:["Build a project that demonstrates your knowledge by the end of Week 12"],
    class:"mobile-slide-edit"
  }
]

class SlideCards extends Component {
  constructor(props) {
    super(props);
    
    this.state = {
      visible:false,
      widthList:[],
      matrix:0,
      last_index:1,
      next_index:1,
      index:0
    };
  }



  componentWillMount(){  
    this.updSlide = this.updSlide.bind(this)
  }

  componentDidMount(){  
   
  }

 
  updSlide(i){   
    if(i!==this.state.index){
      var tr = this.state.index-i>0?980:-980;
      this.setState({matrix:tr,next_index:i})
      setTimeout(()=>{
        var nI = i+1<data.length?i+1:0;
        var lnI = this.state.next_index;
        this.setState({index:i,next_index:nI,matrix:0,last_index:lnI})
      },100);
    }    
  }

  ref = r => {
    this.SM = r
  }
  

  render() {        
    var slides_container = {overflow: 'hidden', position: 'relative', padding: '25px 0', height: '200px'}
    var slides_control = {position: 'relative', transform: `matrix(1, 0, 0, 1, 0, 0)`}
    const {next_index, index, matrix} = this.state;
    const {technology} = this.props;
    const {updSlide} = this;
    return (
        <section id="section_slider-cards" section-index="Program Structure" style={{marginTop:'60px'}} className="section_slider-cards">  
          <div className="intro-label-title tablet--10-12 --auto--margin tablet--auto--margin">
              <h2 >Program Structure</h2>              
          </div> 
          <div className="slides-container" style={slides_container}>
            <div className="slides-control" style={slides_control}>
            {data.map((sl,i)=>{
              var classN = `tabs-tab`
              var sliderStyle = {display: 'none', zIndex: 0,  visibility: 'hidden', opacity: 0, transform: 'matrix(1, 0, 0, 1, 0, 0)'};
              if(0===i){
                classN += ` tabs-tab-first`             
              } 
              if(index===i){
                sliderStyle = {display: 'block', zIndex: 10, visibility: 'inherit', opacity: 1, transform: `matrix(1, 0, 0, 1, ${matrix}, 0)`};  
                classN += ` tab-is-active`             
              }
              if(next_index===i){
                sliderStyle = {display: 'block', zIndex: 0, visibility: 'hidden', opacity: 1, transform: `matrix(1, 0, 0, 1, ${matrix*-1}, 0)`};
              }
              return(
                  <div key={i} className={classN} data-card-index={`${i}`} style={sliderStyle}>
                  {index===i?
                    <div className="tabs-label js-tab-text">
                      {sl.img?
                      <div>
                        <img src={sl.img} alt="" width="300px"/>
                      </div>
                      :null}
                      <div className="tab-label-title"><span >{sl.title}</span></div>
                      {sl.description?sl.description.map((des,ix)=>{
                        return(
                            <span key={ix} className="tabs-label-caption">{des}</span>
                        )
                      }):null}                      
                    </div>:null}
                  </div>
                )
            })}
            </div>
            </div>
            <ul className="slides-pagination" style={{zIndex: 100}}>
              {data.map((sl,i)=>{
                var classN = index===i?"slides-pagination-item active":"slides-pagination-item";                
                return(
                  <li  key={i} className={classN}>
                    <a className="slides-pagination-link" data-slides-pagination-item={`${i}`} onClick={()=>updSlide(i)}></a>
                  </li>
                )
              })}
            </ul>
            <div style={{height:'100px'}}>             
            </div>
          </section>
      );
     
  }  
}

function mapStateToProps(state, ownProps) {
  return {       
    technology: state.common.technology
    
  };
}
function mapDispatchToProps(dispatch) {
  return {     
    commonActions: bindActionCreators(commonActions, dispatch)
  };
}

export default connect(mapStateToProps,mapDispatchToProps)(SlideCards);


